export const getMonthNameInGenitive = (
  arr: MonthData[],
  monthName: string,
): MonthData => {
  const foundMonth = arr.find((i) => i.monthName === monthName);
  return (
    foundMonth || { monthName: '', monthNumber: '', monthNameInGenitive: '' }
  );
};
